<template>
<div class="box">
  <div class="main">
    <div class="head">
      <img @click="share" class="invitation" src="../../assets/images/activity/51wawa5.png" />

      <div class="center">
        <img :src="headimg"/>
        <a>{{name}}</a>
        <span>还剩{{number}}次</span>
      </div>
      <img @click="friendslist" class="friends" src="../../assets/images/activity/51wawa6.png" />
    </div>

    <div class="operation">
      <div class="things" id="things">
        <div class="orderimg" @click="orderBtn">
          <img src="../../assets/images/activity/rightorder.png"/>
          <p>被抓记录</p>
        </div>
        <div class="clawsbody" id="clawsbody">
          <img class="clawstop" src="../../assets/images/activity/51wawa14.png"/>
          <img id="clawscenter" class="clawscenter" src="../../assets/images/activity/51wawa15.png"/>
          <img class="clawsend" :src="loosen"/>
          <img v-show="giftboxShow" class="giftbox" src="../../assets/images/activity/51wawa18.png"/>
        </div>
        <img v-for="(i,index) in imgList" :class="i.gift" :src="i.img_url"/>
      </div>
    </div>
    <p class="jiang">次数别浪费，邀请好友添加奖品——抓壕奖！</p>
    <div class="foot">
      <div class="remote" id="remote">
        <div class="ball" id="ball" @touchmove="touchMove" @touchend="touchEnd"></div>
      </div>
      <div class="catch" @touchstart="touchDown" @touchend="touchUp">
        <img v-show="upShow" src="../../assets/images/activity/51wawa9.png"/>
        <img v-show="downShow" src="../../assets/images/activity/51wawa10.png"/>
      </div>
    </div>

    <!--分享提示-->
    <div v-show="shareShow" class="share" @click="cancelalert">
      <div class="fen">
        <ul>
          <li>快来喊小伙伴来玩游戏!</li>
          <li>领取娃娃大奖！</li>
        </ul>
      </div>

      <!--<div class="social-share">-->
      <!--<a href="javascript:;" class="social-share-icon icon-heart"></a>-->
      <!--</div>-->
    </div>
    <!--提示框-->
    <div class="modal" v-show="modalShow">
      <div class="modalbody">
        <div class="delete">
          <img @click="del" src="../../assets/images/activity/51wawa41.png"/>
        </div>
        <p>{{info}}</p>
      </div>
    </div>
    <!--添加记录-->
    <div class="orderModal" v-show="orderShow">
      <div class="orderbody">
        <div class="delete">
          <img @click="delorder" src="../../assets/images/activity/51wawa41.png"/>
        </div>
        <ul>
          <li v-for="i in friends">
            <div><img :src="i.img_url"/><a>{{i.user_nick}}</a></div>
            <div v-if="i.prop_id == 1">{{i.num}}钻</div>
            <div v-if="i.prop_id == 2">悠悠兔x{{i.num}}</div>
            <div v-if="i.prop_id == 3">{{i.num}}钻</div>
          </li>
        </ul>
      </div>
    </div>
    <div class="resultmodal" v-show="resultShow">
      <div class="modalbody">
        <div class="delete">
          <img @click="del" src="../../assets/images/activity/51wawa41.png"/>
        </div>
        <img class="slide" src="https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa33.png"/>
        <p>{{resultinfo}}</p>
      </div>
    </div>
    <div class="footer">
      <p @click="activitieHome"><a>活动首页</a></p>
      <p @click="usercenter"><a>疯狂抓抓抓</a></p>
    </div>
  </div>

</div>
