          {"gift":"gfts13","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa29.png","type":2},
          {"gift":"gfts15","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/bluedia2.png","type":1},
          {"gift":"gfts16","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/yellowdia3.png","type":1},
          {"gift":"gfts17","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/bluedia2.png","type":1},
          {"gift":"gfts18","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa17.png" ,"type":3},
          {"gift":"gfts19","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},
          {"gift":"gfts20","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},

        ],
        dollList:[
          {"gift":"gfts1","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa17.png" ,"type":3},
          {"gift":"gfts2","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa18.png","type":3},
          {"gift":"gfts3","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa19.png","type":1},
          {"gift":"gfts4","img_url":"https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa20.png","type":1},
