    components: {
    },
    created:function () {


    },
    mounted:function () {
      var _this = this;
      var userid = getCookie("userId");
      var user_nick = getCookie("name");
      var headimg = getCookie("headimg");
      if(headimg == "defalut"){
        headimg = ""
      }else{
        headimg = getCookie("headimg")
      }
      $.ajax({
        type: 'POST',
        url: urlhost+"active/initactive",
        data$Type: "jsonp",
        async:false,
        data: {
          "user_id":userid,
          "user_nick":user_nick,
          "img_url":headimg
        },
        success: function (response) {
          var status = JSON.parse(response).status;
          var data = JSON.parse(response).message;
          console.log(data)
          if(status == 0){
            _this.$layer.msg("进入房间失败，请重新进入")
          }else if(status == 3){
            _this.modalShow = true;
            _this.info = "活动维护暂停中"
          }else{
            setCookie("gnum",data.gnum)
            setCookie("num",data.num)
            setCookie("tnum",data.tnum)
            setCookie("znum",data.znum)
          }
        },
      });

      var name = getCookie("name");
      var num = getCookie("num");
      var znum = getCookie("znum");
      var headimg = getCookie("headimg");
      if(headimg == "defalut"){
        _this.headimg = _this.defalut;
      }else{
        _this.headimg = headimg;
      }
      _this.name = name;
      _this.number = num;
