        type: 'POST',
        url: urlhost+"active/initprops",
        data$Type: "jsonp",
        async:false,
        data: {
          "user_id":userid
        },
        success: function (response) {
          var status = JSON.parse(response).status;
          console.log(JSON.parse(response))
          if(status == 0){
            return
          }else{
            var diamonds = [];
            var gifts = [];
            var dolls = [];
            var dataArray = JSON.parse(response).message;
            dataArray.forEach(function (i) {
              switch (i.prop_type){
                case 1:
                  _this.dollList.forEach(function (j) {   //总图片
                    if(j.type == 1){
                      diamonds.push(j)
                    }
                  })
                  break;
                case 2:
                  _this.dollList.forEach(function (k) {   //总图片
                    if(k.type == 2){
                      dolls.push(k)
                    }
                  })
                  break;
                case 3:
                  _this.dollList.forEach(function (l) {  //总图片
                    if(l.type == 3){
                      gifts.push(l)
                    }
                  })
                  break;
              }
            })
            if(dolls.length >0 ){
              var g = Math.floor((Math.random()*dolls.length));
              _this.imgList.push(dolls[g])
            }
            if(gifts.length >0 ){
              var a = Math.floor((Math.random()*gifts.length));
              _this.imgList.push(gifts[a])
            }
            if(diamonds.length >0 ){
              var d = Math.floor((Math.random()*diamonds.length));
              _this.imgList.push(diamonds[d])
            }
          }

        },
      });
