      var url = window.location.href.split("#")[0];
      $.ajax({
        type: 'POST',
        url: urlhost+"share/index",
        data$Type: "jsonp",
        async:false,
        data:{
          url:url
        },
        success: function (data) {
          var mes = JSON.parse(data).wx_js_config;
          wx.config(JSON.parse(mes));
        },
      });
      wx.ready(function(){
//        var muserid = getCookie("muser_id");
        var aa = window.location.href.split("#")[0];
        var bb = aa.split("?")[0];
        var link = bb.substring(bb.length-1,"")+"?mmuser_id="+getCookie("muser_id")+"&user_id="+userid+"#/activity/invitation";
        var shareData = {
          title: '五一抓娃娃集训营，敬爱劳动的你',
          desc: '40cm时尚动漫娃娃，等你来领取，还有免费钻石赠送让你抓到手软！',
