        var _this = this;
        var userid = getCookie("userId");
        if(_this.down == true){
          _this.upShow = false;
          _this.downShow = true;
          _this.down = false;

          $.ajax({
            type: 'POST',
            url: urlhost+"active/index",
            data$Type: "jsonp",
