              "user_id":userid,
            },
            success: function (response) {
              var data = JSON.parse(response);
              console.log(data)
              if(data.status == 0){
                _this.info = data.message;
                _this.modalShow = true;
              }else{
                setCookie("num",data.message.snum)
                _this.number = data.message.snum;
                $("#clawscenter").animate({"height":"200px"},3000,function () {
                  $(".clawsend").attr("src",tighten);
                  _this.giftboxShow = true;
                  $("#clawscenter").animate({"height":"10px"},3000,function () {
                    $(".clawsend").attr("src",loosen);
                    _this.giftboxShow = false;
                    _this.down = true;
                    _this.resultShow = true;
                    _this.resultinfo = data.message.prop
                  })
                })
              }
            },
          });

        }else{
          return
        }
      },
      touchUp:function () {
        var _this = this;
        _this.upShow = true;
        _this.downShow = false;
      },
      resultfun:function (urlsearch,youid) {

      },
      touchMove:function () {
        var _this = this;
        event.preventDefault();
        var l_dir=-1;
        var box = document.getElementById('remote');
        var touchDiv = document.getElementById('ball');
        var touch = event.targetTouches[0];
        var x = (touch.pageX-box.offsetLeft) - touchDiv.offsetWidth/2;
        var cx = document.getElementById("clawsbody").offsetLeft;
        var wx = document.getElementById("clawsbody").offsetWidth;
        var tx = document.getElementById("things").offsetWidth;
        if( x < 0 ){
          x=0;
          l_dir=2;
          if(g_dir !=l_dir){
            clearInterval(_this.timer);
            g_dir = l_dir;
            _this.timer = setInterval(function () {
              _this.num--;
              if(_this.num <= 0){
                _this.num = 0;
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }else{
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }

            },30)
          }
        }else if(x>box.offsetWidth-touchDiv.offsetWidth){
          x=box.offsetWidth-touchDiv.offsetWidth;
          l_dir=0;
          if(g_dir !=l_dir){
            clearInterval(_this.timer)
            g_dir = l_dir;
            _this.timer = setInterval(function () {
              _this.num ++;
              if(_this.num >= tx - wx){
                _this.num = tx - wx;
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }else{
                document.getElementById("clawsbody").style.left = _this.num +"px"
              }
          },30)

          }


        }

        touchDiv.style.left = x+'px';
      },
      touchEnd:function () {
          var _this = this;
        var box = document.getElementById('remote');
        var touchDiv = document.getElementById('ball');
        touchDiv.style.left = box.offsetWidth / 2 - touchDiv.offsetWidth / 2 + 'px';
        clearInterval(_this.timer)
        g_dir=-1;
      },
      activitieHome:function () {
        var _this = this;
        _this.$router.push({path:"/activity/index"});
        console.log(1)
      },
      usercenter:function () {
        var _this = this;
        _this.$router.push({path:"/"})
      }
    }
  }
</script>

